<template>
  <div class="hello">
    <p 
      v-for="topic in topics.data2" 
      :key="topic.logo" 
      class="logo">      
      <img 
        :src="topic.logo" 
        alt="logo">
    </p>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';

const fetchInitialData = store => store.dispatch('getTopics');

export default {
  prefetch: fetchInitialData,
  computed: {
    ...mapGetters({
      topics: 'getTopics'
    })
  },
  mounted() {
    fetchInitialData(this.$store);
  }
};
</script>
